main {
  margin: 0 auto;
}

h1 {
  margin: 20px;
}

.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 80vh;
  background-color: #f1f1f1;
  border-radius: 10px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  margin: 20px 0px;
  padding: 10px 0;


  &-image {
    width: 100%;
    height: auto;
    object-fit: cover;

    &:hover {
      transform: scale(1.05);
    }

    &-container {
      width: 80%;
      max-width: 600px;
      margin-bottom: 30px;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    }
  }

  &-title-container {
    width: 100%;
    min-height: 500px;
    background: url('/../public/images/tech.png') center center no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    color: #fff;
    text-align: center;
  }

  &-content {
    width: 80%;
    max-width: 1200px;
    margin: 20px;
    text-align: center;
  }

  &-title {
    font-size: 2rem;
    margin-bottom: 10px;
  }

  &-description {
    font-size: 1rem;
    margin-bottom: 20px;
  }
}